.App {
  text-align: none;
}

.actor-bio{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.loader-img{
  width: 20%;
  height: 50%;
  border-radius: 50%;
}
.profile{
color:lightslategray;
justify-self: flex-start;
}
.logo {
  height: 4rem;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Actorcover{
position: relative;
margin-bottom: 2%;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 200px;
  opacity: 0;
  transition: .5s ease;
  background-color: black;
  border-radius: 20px;
}
.Actorimage{
  display: block;
  width: 200px;
  height: 200px;
  border-top:5px solid green;
  border-radius: 20px;
}

a:hover{
  color:green;
  text-decoration:none
}
.Actortext{
font-size: 14px;
margin: 15%;
display: flex;
color:white;
justify-content: center;
align-items: baseline;
}
.Actorlink{
font-weight: 900;
font-size: 38px;
margin: 40%;
display: flex;
color:white;
justify-content: center;
align-items: baseline;
}

.navbar-dark .navbar-toggler {
  border:none
}

button:focus{
outline: none;
}
.Actorcover:hover .overlay{
  rotate:90deg;
  opacity: 0.75;
  /* padding: 5%; */
  }

.Actorcover:hover{
    animation: active-cover  2s linear;
    }

.active{
  color:white;
}
.Actorimage:hover{
padding:3;
}
.loader{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
}

@keyframes active-cover {
  from {
     transform: rotate(0deg)
     
     /* rotate:0deg */
  }
  to {
    transform: rotate(5deg);
    /* rotate:90deg */

  }
}

@media only screen and (max-width: 768px) {
  .overlay {
    height: 100%;
    width: 92%;
  }
  .Actorimage{
    width: 100%;
    height: 300px;
  }
}
